<template>
    <div id="merchant-current">
        <public-header title="账户明细"></public-header>
        <div class="content">
            <div class="wrap-search wrap-search-input">
                <div class="tab-group flex-lr border-1px-b">
                    <div class="tab-item" :class="{active: filter.items.trade_type == 0}" @click="onToggleStatus(0)">全部</div>
                    <div class="tab-item" :class="{active: filter.items.trade_type == 1}" @click="onToggleStatus(1)">订单入账</div>
                    <div class="tab-item" :class="{active: filter.items.trade_type == 2}" @click="onToggleStatus(2)">申请提现</div>
                    <div class="tab-item" :class="{active: filter.items.trade_type == 3}" @click="onToggleStatus(3)">其他变动</div>
                    <div class="tab-item" :class="{active: filter.items.trade_type == 4}" @click="onToggleStatus(4)">买单订单</div>
                </div>
                <van-search placeholder="请输入商户名称" v-model="filter.items.keyword" />
            </div>
            <van-pull-refresh v-model="isRefresh" @refresh="onPullDownRefresh()">
            <van-list
                    v-model="records.loading"
                    :finished="records.finished"
                    @load="onLoad"
                    :offset="100"
                    :immediate-check="false"
                    v-if="!records.empty"
                    class="current-list"
            >
                <div class="current-item margin-10-b" v-for="(item, index) in records.data" :key="item.id">
                    <div class="current-title">
                        <div class="current-img">
                            <img class="img-100" :src="item.logo" alt="">
                        </div>
                        <div class="padding-10-lr c-default font-bold font-16">{{item.store_title}}</div>
                    </div>
                    <div class="border-1px-t padding-10">
                        <div class="current-detail font-14 padding-10-b">
                            <span class="font-bold c-default" v-if="item.trade_type == 1">订单入账</span>
                            <span class="font-bold c-default" v-else-if="item.trade_type == 2">申请提现</span>
                            <span class="font-bold c-default" v-else-if="item.trade_type == 3">其他变动</span>
                            <span class="font-bold c-default" v-else-if="item.trade_type == 4">买单订单入驻</span>
                            <span class="font-bold c-danger" v-if="item.fee > 0">+{{item.fee}}</span>
                            <span class="font-bold c-gray" v-else>{{item.fee}}</span>
                        </div>
                        <div class="current-detail font-14">
                            <span class="c-gray">{{item.addtime_cn}}</span>
                            <span class="c-gray">余额：{{item.amount}}</span>
                        </div>
                    </div>
                </div>
                <div class="loaded" v-if="records.finished">
                    <div class="loaded-tips">没有更多了</div>
                </div>
            </van-list>
            <div class="no-data" v-else>
                <img src="static/img/order_no_con.png" alt="">
                <p>没有符合条件的数据!</p>
            </div>
            </van-pull-refresh>
        </div>
        <iloading v-if="showPreLoading"></iloading>
    </div>
</template>

<script>
	import publicHeader from "@/components/header"
	import {getRecords} from "@/controller/funcCommon"
	export default {
		data() {
			return {
				records:{
					page: 1,
					psize: 20,
					loading: false,
					finished: false,
					empty: false,
					data:[]
				},
				showPreLoading:true,
				isRefresh: false,
				filter: {
					items: {
						trade_type: '0'
                    }
				}
			}
		},
		components: {
			publicHeader,
		},
		methods: {
            onLoad(force){
				getRecords({
					vue: this,
					force: force,
					url: 'plateform/merchant/current/list'
				});
            },
			onPullDownRefresh(){
				this.onLoad(true);
			},
			onToggleStatus(value){
				value = parseInt(value);
				if(this.filter.items.trade_type == value) {
					return;
				}
				this.filter.items.trade_type = value;
            }
		},
		mounted(){
			this.onLoad();
		},

		watch: {
			filter: {
				handler(newValue, oldValue) {
					this.onLoad(true)
				},
				deep: true
			},
		}
	}
</script>

<style>
    #merchant-current .content .van-search{
        padding: 10px;
    }
    #merchant-current .content .current-item{
        background-color: #fff;
    }
    #merchant-current .content .current-item .current-title{
        display: flex;
        flex-wrap: nowrap;
        justify-content: flex-start;
        align-items: center;
        padding: 8px 10px;
    }
    #merchant-current .content .current-item .current-title .current-img{
        width: 30px;
        height: 30px;
    }
    #merchant-current .content .current-item .current-title .current-img img{
        border-radius: 100%;
    }
    #merchant-current .content .current-item .current-detail {
        display: flex;
        flex-wrap: nowrap;
        justify-content: space-between;
        align-items: center;
    }
</style>


